.footer{
    width: 100%;
    height: 200px;
    background-color: white;
    margin: auto;
    overflow: auto;
}
.social-icons{
    width: 300px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    display: flex;
    justify-content:space-between;
}
.link-footer{
    display: inline;
    text-decoration: none;
    border-right: 1px solid black;
    padding-right: 20px;
}
.footer-links{
    max-width: 300px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    
}
.last{
    padding-right: 0px;
    border-right: none;
}