
@media(max-width:820px) {
    .container-member{
        width: fit-content;
        margin: auto;
    }
    .row-member{
        display: inline-block;
        width: fit-content;
        background-color: #f2f2f2;
        display: block;
        width: 100%;
        border-radius: 10px;
        margin-top: 30px;
    }
    .circle-member{
        background-color: white;
        width: 300px;
        height: 300px;
        border-radius: 50%;
        margin-right: 10px;
        margin-left: 10px;
        margin-top: 20px;
        margin-bottom: 20px;
    }
}
@media(min-width:820px){
    .row-member{
        border: 1px solid black;
        display: inline-block;
        width: fit-content;
        background-color: white;
        display: flex;
        justify-content: space-between;
        width: 820px;
        border-radius: 10px;
        margin-top: 30px;
    }
    .circle-member{
        background-color: #f2f2f2;
        width: 250px;
        height: 250px;
        border-radius: 50%;
        margin-right: 10px;
        margin-left: 10px;
        margin-top: 20px;
        margin-bottom: 20px;
    }
    .container-member{
        width: fit-content;
        margin: auto;
    }
}
@media(min-width:1400px){
    .row-member{
        border: 1px solid black;
        display: inline-block;
        width: fit-content;
        background-color: white;
        display: flex;
        justify-content: space-between;
        width: 1400px;
        border-radius: 10px;
        margin-top: 30px;
    }
    .circle-member{
        background-color: #f2f2f2;
        width: 300px;
        height: 300px;
        border-radius: 50%;
        margin-right: 10px;
        margin-left: 10px;
        margin-top: 20px;
        margin-bottom: 20px;
    }
    .container-member{
        width: fit-content;
        margin: auto;
    }
}