.wrapper-section1{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url('./pictures/bg-intro-mobile.svg');
    background-repeat: no-repeat;
    background-position:top right ;
}
.text-section{
    display: flex;
    margin: auto;
    flex-direction: column;
    align-items: center;
    max-width: 90%;
}
.text-section h1{
    color: rgb(0,0,0);
    font-size: 50px;
}
.text-section p{
    margin-top: 30px;
    margin-bottom: 30px;
}
#section1img{
    margin-top: 20px;
    width: 100%;
    max-width: 600px;
    max-height: 600px;
}
.button-section1{
    border: 1px solid black;
    padding-left:15px;
    padding-right:15px;
    padding-top: 8px;
    padding-bottom: 8px;
    cursor: pointer;
    border-radius: 15px;
}
